header {
  width: 100%;
  background: transparent;
  .inner {
    width: 1180px; height: 80px;
    margin: 0 auto;
    position: relative;

    h1 {
      position: absolute;
      bottom: 20px; left: 0;
      a {
        font: bold 24px/1 'arial';
        color: #000;
      }
    }
		#gnb {
			position: absolute;
			bottom: 0px;
			right: 0px;
			li {
				float: left;
				transition: 0.3s;
				a {
					display: inline-block;
					padding: 30px 30px;
					font: bold 14px/1 'arial';
					color: #000;
				}
				&:hover {
					transform: scale(1.2) rotate(10deg);
				}
			}
		}
    .fa-bars {
      font-size: 24px;
      position: absolute;
      bottom: 20px; right: 40px;
      display: none;
    }
  }
}

@media screen and (max-width: $tablet) {
  header {
    width: 100%;
    .inner {
      width: 100%;
      h1 {
        bottom: 20px; left: 40px;
      }
      #gnb {
        display: none;
      }
      .fa-bars {
        display: block;
      }
    }
  }
}